import React, { useState } from 'react';
import "../index.css"


function getRandomNum(min:number, max:number) {
    return Math.floor(Math.random() * ((max - min) + 1) + min);
  }
  
const Dice = ():any => {
    const [dice, setDice] = useState (0);
    return(
        //#a8334a
        <>
        <button className = "button-design" style={{height:80, width: 200, fontSize:26, fontFamily: 'Arial', backgroundColor:"#cf8090"}} onClick={() => setDice(getRandomNum(1, 6))}>
            Roll The Dice
        </button>
        <h1>
            {dice}
        </h1>

        </>

    );
}
export default Dice;